import axios from "axios";

export function equipmenttype_equipments(queryString) {
  return axios({
    method: "GET",
    url:
      `https://13.124.69.255:90/api/equipmenttype/GetEquipType_List?${queryString}` +
      queryString,
    mode: "no-cors",
  });
}
export function create_equipmenttype_equipment(body) {
  return axios({
    method: "POST",
    url: "https://13.124.69.255:90/api/equipmenttype_detail/Save_EquipmentType",
    data: body,
    mode: "no-cors",
  });
}
export function update_equipmenttype_equipment(body) {
  body.saveType = "update";
  return axios({
    method: "POST",
    url: "https://13.124.69.255:90/api/equipmenttype_detail/Save_EquipmentType",
    data: body,
    mode: "no-cors",
  });
}
export function delete_equipmenttype_equipment(body) {
  return axios({
    method: "DELETE",
    url: "https://13.124.69.255:90/api/equipmenttype/Delete_EquipmentType",
    data: { idxs: [`${body.equipment_type_idx}`] },
    mode: "no-cors",
  });
}
